@import url(https://fonts.googleapis.com/css?family=Ubuntu:500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand:600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.TopName {
  background-color: #282c34;
  text-align: right;
  color: whitesmoke;
  vertical-align: middle;
  font-size: 5mm;
  font-weight: bold;
  font-family: 'Helvetica Neue';
  padding-top: 1mm;
  padding-bottom: 2mm;
  padding-right: 3mm;
}

.navbar {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  justify-content: left;
  color: whitesmoke;
  vertical-align: middle;
  align-items: center;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li,
a,
button,
div {
  font-family: 'Ubuntu';
  font-weight: 500;
  font-size: 16px;
  color: #edf0f1;
  text-decoration: none;
}

header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 15px;
  background-color: #0a1e2c;
}

.name {
  color: whitesmoke;
  font-size: 20px;
  font-family: 'Ubuntu';
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  vertical-align: middle;
}

.seal {
  max-width: 55px;
  height: auto;
  vertical-align: middle;
  text-align: flex-end;
  padding-right: 20px;
}

.navlinks {
  list-style: none;
}

.navlinks li {
  display: inline-block;
  justify-content: flex-start;
  padding: 0px 10px;
}

.navlinks li a {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.navlinks li a:hover {
  color: rgba(101, 160, 177, 0.8);
}

button {
  margin-left: 20px;
  padding: 9px 25px;
  background-color: rgb(150, 181, 211);
  border: none;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.contact {
  list-style: none;
}

button:hover {
  background-color: rgba(101, 160, 177, 0.8);
}

.divider {
  height: 1px;
  background-color: rgb(150, 181, 211);
  opacity: 0.4;
  position: relative;
  margin-bottom: 10px;
}

.textHeading {
  font-family: 'Quicksand', sans-serif;
  font-size: 60px;
  color: rgba(65, 136, 194, 0.8);
  text-align: left;
  padding-left: 50px;
  padding-top: 50px;
}

.textBody {
  font-family: 'Roboto';
  font-size: 20px;
  color: black;
  padding-left: 100px;
  padding-bottom: 50px;
  padding-right: 100px;
  padding-top: 20px;
}

.contactInfo {
  font-family: 'Roboto';
  color: rgba(72, 96, 116, 0.8);
  font-size: 20px;
  border-bottom: 1px dotted #000;
  text-decoration: none;
}

.contact a:hover {
  color: rgba(101, 160, 177, 0.8);
}

.contact a {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

/* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
.flip-card {
  background-color: transparent;
  width: 370px;
  height: 500px;

  -webkit-perspective: 1000px;

          perspective: 1000px; /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.flip-card:hover .flip-card-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.flip-card-front {
  background-color: transparent;
}

/* Style the back side */
.flip-card-back {
  background-color: rgb(36, 50, 63);
  color: white;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

html,
body {
  height: 100%;
}
body {
  background: radial-gradient(
    ellipse at center,
    rgb(102, 168, 170) 0%,
    rgb(103, 173, 213) 20%,
    #96dde0 100%
  );
  overflow: hidden;
}

.ocean {
  height: 5%;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: #015871;
}

.wave {
  background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/85486/wave.svg)
    repeat-x;
  position: absolute;
  top: -198px;
  width: 6400px;
  height: 198px;
  -webkit-animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
          animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  top: -175px;
  -webkit-animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite,
    swell 7s ease -1.25s infinite;
          animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite,
    swell 7s ease -1.25s infinite;
  opacity: 1;
}

@-webkit-keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@-webkit-keyframes swell {
  0%,
  100% {
    -webkit-transform: translate3d(0, -25px, 0);
            transform: translate3d(0, -25px, 0);
  }
  50% {
    -webkit-transform: translate3d(0, 5px, 0);
            transform: translate3d(0, 5px, 0);
  }
}

@keyframes swell {
  0%,
  100% {
    -webkit-transform: translate3d(0, -25px, 0);
            transform: translate3d(0, -25px, 0);
  }
  50% {
    -webkit-transform: translate3d(0, 5px, 0);
            transform: translate3d(0, 5px, 0);
  }
}

